body {
  color: var(--first-text-color);
  font-family: DM Sans, sans-serif;
  font-size: 16px;
}

.link {
  text-decoration: none;
}

.list {
  list-style: none;
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

ul, ol {
  margin: 0;
  padding-left: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

html {
  scroll-behavior: smooth;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 320px) {
  .container {
    max-width: 320px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

.title {
  text-align: center;
  letter-spacing: .04em;
  text-transform: uppercase;
  font-family: Titan One;
  font-size: 34px;
  font-weight: 400;
  line-height: 1.15;
}

@media screen and (min-width: 768px) {
  .title {
    font-size: 48px;
  }
}

@media screen and (min-width: 1280px) {
  .title {
    letter-spacing: .06em;
    font-size: 58px;
    line-height: 1.14;
  }
}

.no-scroll {
  overflow: hidden;
}

@media screen and (min-width: 768px) {
  .no-scroll {
    overflow: auto;
  }
}

.is-hidden-mob {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.mob-menu {
  height: 100%;
  width: 100%;
  z-index: 10;
  background-color: #e17992;
  padding: 60px 0 0;
  transition: transform 1s linear, opacity 1s, visibility 1s linear;
  position: fixed;
  top: 0;
  transform: translateX(0);
}

@media screen and (min-width: 768px) {
  .mob-menu {
    width: 254px;
    right: 0;
  }
}

@media screen and (min-width: 788px) {
  .mob-menu {
    width: 50%;
  }
}

.mob-menu.is-hidden-mob {
  transform: translateX(100%);
}

.button__close-mob-menu {
  height: 33px;
  width: 33px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 50%;
  outline: none;
  justify-content: center;
  align-items: center;
  transition: background-color .3s linear;
  display: flex;
  position: absolute;
  top: 20px;
  right: 20px;
}

.button__close-mob-menu:hover, .button__close-mob-menu:focus {
  background-color: #d41443;
}

li {
  list-style: none;
}

.mob-menu-list-item {
  border-top: 1px solid #e18298;
  padding: 13px 0 11px 20px;
}

.mob-menu-list-item:last-child {
  border-bottom: 1px solid #e18298;
  padding-bottom: 14px;
}

.mob-menu-btn {
  color: #d41443;
  width: 209px;
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: 22px;
  justify-content: center;
  align-items: center;
  margin-bottom: 19px;
  margin-left: auto;
  margin-right: auto;
  padding-top: 15px;
  padding-bottom: 15px;
  font-family: DM Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 21px;
  transition: background-color .3s linear;
  display: flex;
}

.mob-menu-btn:hover, .mob-menu-btn:focus {
  background-color: #ecebeb;
}

.mob-menu-btn span {
  margin-left: 40px;
}

.mob-menu-list-item__link {
  text-align: left;
  color: #fff;
  font-family: DM Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 18px;
  text-decoration: none;
  transition: color .3s linear;
}

.mob-menu-list-item__link:hover, .mob-menu-list-item__link:focus {
  color: #d41443;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

:root {
  --first-text-color: #fff;
  --second-text-color: #d41443;
  --third-text-color: #ffa5ba;
  --fourth-text-color: #000;
  --fifth-text-color: #907e82;
  --top-body-bg-color: #e5e5e5;
  --bottom-body-bg-color: #fff5f6;
  --first-bg-color: #ffb8ca;
  --second-bg-color: #f0d1a5;
  --third-bg-color: #c2e297;
  --modal-bg-color: #e17992;
}

