  height: 26px;
  background-color: #fff;
  background-image: url("arrow.b2b31464.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 10px;
  border-radius: 50%;
  margin-top: 7px;
  transition: transform .7s linear, border .3s linear;
  display: block;
}

