.infoinfo-open-btn:hover.infoinfo-open-btn:after, .infoinfo-open-btn:focus.infoinfo-open-btn:after {
  border: 2px solid rgba(212, 20, 68, .584);
}

.info-open-btn__check:checked + .infoinfo-open-btn:after {
  transform: rotate(-90deg);
}

.info-open-btn__check:checked + .infoinfo-open-btn .info-text {
